import { Component, OnInit, ViewChild } from '@angular/core';
import { ApiEntrenaService } from 'src/app/Services/Api/ApiEntrena.service';
import { ToastService } from 'src/app/Services/Utilities/Toast.service';
import { StorageWebService } from 'src/app/Services/Storage/StorageWeb.service';
import { ActivatedRoute, ChildActivationEnd, Router } from '@angular/router';
import { ModalController, IonSlides, IonContent, LoadingController, AlertController } from '@ionic/angular';
import { LoadingService } from 'src/app/Services/Utilities/Loading.service';
import { DataComparnyService } from 'src/app/Services/dataCompany.service';
import { environments } from '../../../../../environments/environment.prod';
@Component({
  selector: 'app-view-design',
  templateUrl: './view-design.page.html',
  styleUrls: ['./view-design.page.scss'],
})
export class ViewDesignPage implements OnInit {

  JSONDesigner = {
    save: false,
    pages: [],
    count: 0,
    prom: 0,
    tot: 0,
    apro: 0
  };

  nameDesing = '';

  viewInit = '1';
  save = false;

  idAnswer = '';

  pagesCount = 0;
  pageActive = 1;

  load = false;

  saveAnswer = false;

  apro = false;

  color = '';
  

  @ViewChild('slide', {static: false}) slide: IonSlides;
  @ViewChild('content', {static: false}) content: IonContent;

  constructor(
    private apiEntrena: ApiEntrenaService,
    private toasCtrl: ToastService,
    private storage: StorageWebService,
    private ActiveRoute: ActivatedRoute,
    private modalCtrl: ModalController,
    private router: Router,
    private loading: LoadingService,
    private loadCtrl: LoadingController,
    private alertCtrl: AlertController,
    private dataCompany: DataComparnyService
    ) { 
    
      this.dataCompany.$color.subscribe((color) => {
          this.color = color;
      })

    }

  ngOnInit() {
  }

  ionViewWillEnter() { // se ejecuta antes de iniciar la vista
    this.loadDesign();
  }

  async loadDesign() { // carga la vista del formulario
    
    const login = await this.storage.getLogin();

    if ( login ) {
        if (login[0].data.role === 'ADMIN_ENTRENA') {
          this.apiEntrena.readDesigner(login[0].token, this.ActiveRoute.snapshot.paramMap.get('id')).subscribe(async (data: any) => {
            console.log(data);
            if (data.status === 'ok') {
              
              this.load = true;
              this.idAnswer = data.response._id;
              this.JSONDesigner = data.response.JSONEntrena;
              this.JSONDesigner.count = 0;
              this.pagesCount = this.JSONDesigner.pages.length;
            
              this.nameDesing = data.response.name;
            } else {
              await this.toasCtrl.newCreatedToast('Could not read design, please try again later', false)
            }
          }, async (error) => {
            await this.toasCtrl.newCreatedToast('Could not read design, please try again later', false)
          })
        } else {
          this.apiEntrena.readAnswerJson(login[0].token, this.ActiveRoute.snapshot.paramMap.get('id')).subscribe(async (data: any) => {
            console.log(data, 'data del usuario');
            if (data.status === 'ok') {
              this.saveAnswer = data.response.saveAnswer ? data.response.saveAnswer : false;
              this.load = true;
              this.idAnswer = data.response._id;
              this.JSONDesigner = data.response.JSONEntrena;
              this.JSONDesigner.count = 0;
              this.pagesCount = this.JSONDesigner.pages.length;
              const pagesquiz = this.JSONDesigner.pages.filter((item) => item.type === 'quiz');
  
              pagesquiz.forEach((item: any) => {
                const children: any[] = item.Chilndren;
                children.forEach((child) => {
                  if (child.type === 'radio' || child.type === 'checkbox') {
                    this.JSONDesigner.count = this.JSONDesigner.count + 1;
                  }
                })
              })
              this.nameDesing = data.response.name;
              console.log(this.idAnswer, 'id de respuesta');
              // tslint:disable-next-line:max-line-length
              this.apiEntrena.createdAnswer(login[0].token, {parentDesigner: this.ActiveRoute.snapshot.paramMap.get('id'), parentAdmin: login[0].data.parentAdmin} ).subscribe((res: any) => {
  
                if (res.status === 'error') {
                  this.toasCtrl.newCreatedToast(res.error, false).then(async () => {
                  })
  
                } else {
                  this.idAnswer = res.response._id;

                  console.log(this.JSONDesigner, 'CHACHAI')

                  if (this.JSONDesigner.tot < this.JSONDesigner.apro && this.saveAnswer) {
                    this.otherDesign();
                    this.apro = false;
                  } else {
                    this.apro = true;
                  }
                }
  
                console.log(this.idAnswer, 'id de respuesta 2');
              }, async (error) => {
                this.toasCtrl.newCreatedToast('No se pudo guardar el intento', false)
              })
  
            } else {
              this.toasCtrl.newCreatedToast('No se pudo guardar el intento', false)
            }
          }, async (error) => {
            this.toasCtrl.newCreatedToast('No se pudo guardar el intento', false)
          })
        }
    }
  }


  
  async otherDesign() {
    const alert = await this.alertCtrl.create({
      header: 'Atención!',
      message: 'Usted no aprobó la prueba en el anterior intento, desea realizar uno nuevo ?',
      backdropDismiss: false,
      buttons: [
        {
          text: 'Cancelar',
          role: 'cancel',
          handler: () => {
            this.router.navigate(['/list-my-designs'])
          }
        },
        {
          text: 'Aceptar',
          handler: async () => {

            const login = await this.storage.getLogin();
        
            if ( login ) {

            // tslint:disable-next-line:max-line-length
            this.apiEntrena.createdAnswerOther(login[0].token, {parentDesigner: this.ActiveRoute.snapshot.paramMap.get('id'), parentAdmin: login[0].data.parentAdmin} ).subscribe((data: any) => {
  
              if (data.status === 'error') {
                this.toasCtrl.newCreatedToast('No se pudo guardar el intento', false)

              } else {
                this.saveAnswer = data.response.saveAnswer ? data.response.saveAnswer : false;
                this.load = true;
                this.idAnswer = data.response._id;
                this.JSONDesigner = data.response.JSONEntrena;
                this.JSONDesigner.count = 0;
                this.pagesCount = this.JSONDesigner.pages.length;
                const pagesquiz = this.JSONDesigner.pages.filter((item) => item.type === 'quiz');
    
                pagesquiz.forEach((item: any) => {
                  const children: any[] = item.Chilndren;
                  children.forEach((child) => {
                    if (child.type === 'radio' || child.type === 'checkbox') {
                      this.JSONDesigner.count = this.JSONDesigner.count + 1;
                    }
                  })
                })
                this.nameDesing = data.response.name;
                this.idAnswer = data.response._id;
                this.toasCtrl.newCreatedToast('Nuevo intento creado', true)
              
              }

            }, async (error) => {
              this.toasCtrl.newCreatedToast('No se pudo guardar el intento', false)
            })
          }
        }
      }
      ]
    })

    await alert.present();
  }
 


  changeSlide() {
    this.slide.getActiveIndex().then((idx) => {
      this.pageActive = idx + 1;
      this.content.scrollToTop().then(() => {});
    })
  }

  prev() {
    this.slide.slidePrev(300).then(() => {
      this.pageActive = this.pageActive + 1;
    })
  }

  next() {
    this.slide.slideNext(300).then(() => {
      this.pageActive = this.pageActive + 1;
    })
  }


  async autoSave() { // Guarda los cambios cada vez que se ejcuta alguna funcion que altera el formulario
    console.log(this.JSONDesigner, 'paso 1');
 
    if (!this.saveAnswer) {
      console.log('eNTRAMOS', this.idAnswer, this.JSONDesigner)
      const login = await this.storage.getLogin();
      this.save = true;
      if ( login ) {

        
        if (login[0].data.role === 'ADMIN_ENTRENA') {
          this.save = false;
        } else {

        // tslint:disable-next-line:max-line-length
        this.apiEntrena.updateAnswer(login[0].token, {_id: this.idAnswer, JSONEntrena: this.JSONDesigner}).subscribe((data: any) => {
          console.log(data, 'data entrena')
          if (data.status === 'ok') {
            this.save = false;
            this.JSONDesigner = data.response.JSONEntrena;
          } else {
            this.save = false;
            this.toasCtrl.newCreatedToast('No se pudo guardar los cambios', false)
          }
        }, (error) => {
          this.save = false;
          this.toasCtrl.newCreatedToast('No se pudo guardar los cambios', false)
        })
    }
  }
    }
  }

  async saveEnd() {
    if (!this.saveAnswer) {
      const login = await this.storage.getLogin();

      if ( login ) {

        if (login[0].data.role === 'ADMIN_ENTRENA') {
          this.save = false;
        } else {

          console.log('ALERTAS')
          const alert = await this.alertCtrl.create({
            header: 'Está seguro de finalizar la capacitación?',
            message: 'Al finalizar la capacitación no podrá realizar cambios en ella de nuevo.',
            buttons: [
              {
                text: 'Cancelar',
                role: 'cancel'
              },
              {
                text: 'Aceptar',
                handler: () => {
                  this.save = true;
                  this.JSONDesigner.tot = Number((this.JSONDesigner.prom / this.JSONDesigner.count).toFixed(1));
                  // tslint:disable-next-line:max-line-length
                  this.apiEntrena.updateAnswerSave(login[0].token, {_id: this.idAnswer, JSONEntrena: this.JSONDesigner}).subscribe((data: any) => {
                    if (data.status === 'ok') {
                      if ( this.JSONDesigner.tot >= this.JSONDesigner.apro) {
                        this.toasCtrl.newCreatedToast('Feclicitaciones, aprobaste el módulo', true)
                      } else {
                        this.toasCtrl.newCreatedToast('Lo sentimos, no te alcanzó el promedio final para aprobar el módulo', false)
                      }
                      this.save = false;
                      this.JSONDesigner = data.response.JSONEntrena;
                      this.router.navigate(['/list-my-designs'])
                    } else {
                        this.save = false;
                        this.toasCtrl.newCreatedToast('No se pudo guardar los cambios', false)
                    }
                  }, (error) => {
                    this.save = false;
                    this.toasCtrl.newCreatedToast('No se pudo guardar los cambios', false)
                  })
                }
              }
            ]
          })


          await alert.present();

       
        // tslint:disable-next-line:max-line-length
      
    }

  }
    }
  }


  insertAnswers(event, page, child, opt) {
    this.JSONDesigner.prom = 0;
    this.JSONDesigner.pages = this.JSONDesigner.pages.map((item: any) => {
      if (item.id === page) {
        item.Chilndren.map((children: any) => {
          if (children.id === child.id) {
            if (child.type === 'radio') {
              children.values.answer.text = event.detail.value;
              const optRadio = children.values.opts.filter((ropt) => ropt.text === event.detail.value);
              console.log(optRadio, 'bien')
              children.values.answer.value = parseFloat(optRadio[0].value);
            } else if (child.type === 'checkbox') {
              
              

              if (event.detail.checked) {
                children.values.answer.sum = children.values.answer.sum + parseFloat(children.values.opts[opt].value);
                if (!children.values.answer.op.find((op) => op === event.detail.value )) {
                  children.values.answer.op.push(event.detail.value);
                }
              } else {
                children.values.answer.sum = children.values.answer.sum - parseFloat(children.values.opts[opt].value);
                children.values.answer.op = children.values.answer.op.filter( op => op !== event.detail.value);
              }

              
              children.values.opts[opt].checked = event.detail.checked;
            }

            
          }
          // tslint:disable-next-line:max-line-length
          if (children.type === 'radio' ||  children.type === 'checkbox') {
            this.JSONDesigner.prom = this.JSONDesigner.prom + parseFloat(children.values.answer.value) + parseFloat(children.values.answer.sum);
          return children;
          }
        });
      }
      return item;
    })
    this.autoSave();

    console.log(event.detail.value, page, child, opt)
  }

  async exportPDF() {
    const login = await this.storage.getLogin();
    this.loading.createLoading('Exportando PDF...');
    if ( login ) {
      // tslint:disable-next-line:max-line-length
     
      this.apiEntrena.exportPDF(login[0].token, this.idAnswer).subscribe((data: any) => {
        console.log(data);
        if (data.status === 'ok') {
          this.loading.cancelLoading();
          // tslint:disable-next-line:max-line-length
          window.open(environments.URL_NODE + '/vtentrena/designer/answer/download?path=' + data.path + '&name=' + 'Diploma -' + login[0].data.name + '.pdf' , '_self');
        } else {
          this.loading.cancelLoading();
          this.toasCtrl.newCreatedToast('Could not save design, please try again later', false)
        }
      }, (error) => {
        this.loading.cancelLoading();
        this.toasCtrl.newCreatedToast('Could not save design, please try again later', false)
      })
  }
  }


  exit(){
    this.router.navigate(['/list-my-designs']);
  }


  download(child) {
    console.log('abrete', child)
    // tslint:disable-next-line:max-line-length
    window.open(environments.URL_NODE + '/vtentrena/designer/answer/download/files?path=' + child.values.src + '&name=' + child.values.src , '_self')
  }
 


}
